<template>
  <div id="search">
    <div class="content">
      <h1 class="title">Search</h1>
      <hr />
      <h3>Want to search book ?</h3>
      <input
        type="text"
        class="input-search"
        placeholder="I want to search..."
        v-model.lazy="inputSearch"
        name="key"
      />
      <h3 class="text-center" @click="toggle()">Or</h3>
      <p class="text-center mt-3">Search By Author</p>
      <select name="authorSelect" v-model="selectAuthors">
        <option v-for="(author,index) in authors" :key="index" :value="author._id">{{ author.name }}</option>
      </select>

      <!-- If user search by option -->
      <div v-if="search.option.name">
        <h3>Result found for {{ search.option.name }} Author</h3>
        <div class="content-wrapper">
          <CardBook
            v-for="(book,index) in search.option.books"
            :key="index"
            :book="book"
            :authorName="search.option.name"
          ></CardBook>
        </div>
      </div>

      <div v-if="loading" class="content-wrapper">
        <SkeletonCard v-for="n in 6" :key="n" />
      </div>

      <div v-if="search.text.length > 0 ">
        <h3>Result found for "{{ inputSearch }}"</h3>
        <div class="content-wrapper">
          <CardBook v-for="(book,index) in search.text" :key="index" :book="book"></CardBook>
        </div>
      </div>
      <!-- Otherwise no results found either from options or text -->
      <div v-else-if="!search.option.name && search.text.length == 0">
        <h3 class="text-danger text-center">Not result found</h3>
      </div>
    </div>
  </div>
</template>

<script>
import CardBook from "../CardBook";

import {
  QUERY_AUTHORS,
  QUERY_AUTHOR_BOOK,
  QUERY_BOOK_TEXT,
} from "@/graphql/query";
export default {
  name: "bookSearch",
  components: {
    CardBook,
    SkeletonCard: () => import("../SkeletonLoad"),
  },
  data() {
    return {
      inputSearch: "",
      authors: [],
      selectAuthors: "",
      loading: false,
      search: {
        option: {},
        text: {},
      },
    };
  },
  async mounted() {
    try {
      const {
        data: { authors },
      } = await this.$apollo.query({
        query: QUERY_AUTHORS,
      });
      return (this.authors = authors);
    } catch (error) {
      console.log(error);
    }
  },
  methods: {
    clearResult() {
      this.search.option = {};
      this.search.text = [];
    },
  },
  watch: {
    // if selectAuthor has change from option input
    async selectAuthors(val) {
      this.clearResult();
      this.loading = true;
      try {
        // Desctruction object data -> author -> books
        const {
          data: { author },
        } = await this.$apollo.query({
          query: QUERY_AUTHOR_BOOK,
          variables: {
            id: val,
          },
        });
        this.loading = false;
        this.search.option = author;
      } catch (error) {
        console.log(error);
      }
    },
    // If user type input text and it's change
    async inputSearch(val) {
      this.clearResult();
      this.loading = true;
      try {
        const {
          data: { txtBook: books },
        } = await this.$apollo.query({
          query: QUERY_BOOK_TEXT,
          variables: {
            key: val,
          },
        });
        this.loading = false;
        this.search.text = books;
      } catch (error) {
        console.log(error);
      }
    },
  },
};
</script>

